import { Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { UsersComponent } from '@components/users/users.component';
import { CreateUserComponent } from './components/users/create-user/create-user.component';
import { UserComponent } from './components/users/user/user.component';
import { OrganizationsComponent } from './components/organizations/organizations.component';
import { CreateOrganizationComponent } from './components/organizations/create-organization/create-organization.component';
import { OrganizationComponent } from './components/organizations/organization/organization.component';
import { GroupsComponent } from './components/groups/groups.component';
import { GroupComponent } from './components/groups/group/group.component';

export const AppRoutes: Routes = [
  {
    path: '*',
    component: UsersComponent
  },
  {
    path: '',
    component: UsersComponent
  },
  {
    path: 'users/create',
    component: CreateUserComponent
  },
  {
    path: 'user/:id',
    component: UserComponent
  },
  {
    path: 'users',
    component: UsersComponent
  },
  {
    path: 'group/:id',
    component: GroupComponent
  },
  {
    path: 'groups',
    component: GroupsComponent
  },
  {
    path: 'organizations/create',
    component: CreateOrganizationComponent
  },
  {
    path: 'organization/:id',
    component: OrganizationComponent
  },
  {
    path: 'organizations',
    component: OrganizationsComponent
  },
  {
    path: '**',
    component: UsersComponent
  }
];
