import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import { timer } from 'rxjs';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit, AfterViewInit {

  @ViewChild('nav') _nav: any;
  @ViewChild('logo') _logo: any;

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    timer(500).subscribe(()=> {
      // console.log('set theme');
      // console.log(this._nav);
      // console.log(this._logo)
      this._nav.nativeElement.setAttribute('header-theme', 'theme5');
      this._logo.nativeElement.setAttribute('logo-theme', 'theme5');
    });
  }

}
